import React from 'react'

import { useForm } from 'react-hook-form';

const Form = () => {

    const { register, handleSubmit, formState: { errors } } = useForm();
    const onSubmit = data => console.log(data);

    return (
        <div className="sign-up-form-wrapper">
            <button className="free-try-btn">
                <strong>Try it free 7 days</strong> then $20/mo. thereafter
            </button>
            <form className="form" onSubmit={handleSubmit(onSubmit)}>
                <input
                    type="text"
                    {...register("firstName", { required: 'First name cannot be empty' })}
                    placeholder="First Name"
                />
                {errors.firstName && <p className="required">{errors.firstName.message}</p>}

                <input
                    type="text"
                    {...register("lastName", { required: 'Last name cannot be empty' })} placeholder="Last Name"
                />
                {errors.lastName && <p className="required">{errors.lastName.message}</p>}

                <input

                    type="email"
                    {...register("email", { required: 'Email cannot be empty' })} placeholder="Email Address"
                />
                {errors.email && <p className="required">{errors.email.message}</p>}

                <input

                    type="password"
                    {...register("password", { required: 'Password cannot be empty' })} placeholder="Password"
                />
                {errors.password && <p className="required">{errors.password.message}</p>}

                <button className="submit-btn" type='submit'>
                    Claim your free trial
                </button>
                <span className="agreement">
                    By clicking the button, you are agreeing to our
                    <strong>
                        <span className="terms"> Terms and Services</span>
                    </strong>
                </span>
            </form>
        </div>
    );

}

export default Form;